
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../server';
export default {
directives: {
TransferDom
},
components:{
SwipeoutItem,
SwipeoutButton,
Swipeout,
Popup,
Confirm
},
data(){
return {
showPop: false,
doctorId:this.$route.params.rid,
doctorInfo:{},
comment:{},
BASEIMGURL:api.BASEIMGURL,
defaultImg: 'this.src="' + require('../assets/icon_yizhu@2x.png') + '"',//
services:[],
patients:[],
orderDetail:{},
checknum:'0',
showConfirm:true,
}
},
mounted(){
api.doctorclinic({doctorid:this.doctorId}).then(res=>{
if(res.code == '000'){
this.doctorInfo = JSON.parse(res.data);
this.comment = this.doctorInfo.comment;
let openService=[];
this.doctorInfo.services.forEach(function (val) {
if(val.code == 'reservationCall'){
openService.push(val);
}
if(val.code == 'freeservice'){
openService.push(val);
}
})
this.services = openService;
}
})
},
methods: {
getPatients(){
api.getpatients({}).then(res=>{
if(res.code == '000'){
this.patients = JSON.parse(res.data);
}
})
},
showPlugin(id,event) {
event.cancelBubble = true;
var _this=this;
this.$vux.confirm.show({
content: '',
onConfirm () {
api.deletepatient({"rid":id}).then(res=>{
if(res.code == '000'){
api.getpatients({}).then(res=>{
if(res.code == '000'){
_this.patients = JSON.parse(res.data);
}
})
}
})
}
})
},

tuwenPopup(){
this.showPop = true;
this.getPatients();
},
checkService(num){
this.checknum = num;
},
enterTalk(id){
var obj={
"patientid":id,
"doctorServiceId":this.services[0].rid
};
api.ordercreate(obj).then(res=>{
if(res.code == '000'){
this.orderDetail=JSON.parse(res.data);
location.href='#/chat/' + this.orderDetail.servicedetailid +'/'+ '1';
api.paycallback({'orderNo':this.orderDetail.orderno}).then(res =>{
if(res.code == '000'){

}
})
}
});

}

}

}



